.second-section{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 170px;
  span{
    font-family: Roboto-Medium;
    font-size: 14px;
  }
  .company{
    width: 60%;
    display: flex;
    justify-content: space-between;
    margin: 35px 0 270px 0;
    img{
      scale: 120%;
      &:last-of-type{
        scale: 120%;
      }
    }
  }
  .content{
    display: flex;
    img{
      width: 50%;
    }
    .text-content{
      width: 50%;
      font-family: Roboto-Medium;
      margin-left: 50px;
      p{
        font-size: 18px;
        margin-bottom: 70px;
        margin-top: 70px;
        &:last-of-type{
          margin: 0;
          width: 60%;
          line-height: 28px;
        }
      }
      h2{
        font-size: 54px;
        font-weight: 400;
        margin-bottom: 90px;
      }
      button{
        margin-top: 65px;
        border: 0;
        width: 150px;
        height: 50px;
        background-color: black;
        color: white;
        font-size: 12px;
        transition: 0.3s;
        &:hover{
          width: 200px;
          border-top-right-radius: 30px;
        }
      }
    }
  }
  video{
    width: 70%;
    margin-top: 50px;
  }
}