.first-section{

  header{
    height: 40px;
    width: 100%;
    background-color: black;
    display: flex;
    align-items: center;
    justify-content: center;
    p{
      margin: 0;
      color: white;
      font-size: 13px;
      font-family: Roboto-Medium;
    }
  }
  .title{
    margin: 55px 0 0 100px;
    display: flex;
    align-items: center;
    .left-title{
      width: 50%;
      h1{
        font-size: 54px;
        font-family: Roboto-Medium;
        font-weight: 400;
        width: 60%;
      }
      p{
        margin: 50px 0 0 180px;
        width: 47%;
        font-size: 18px;
        font-family: Roboto-Medium;
        line-height: 24px;
      }
      button{
        margin: 50px 0 0 180px;
        border: 0;
        width: 140px;
        height: 50px;
        background-color: black;
        color: white;
        font-size: 12px;
        transition: 0.3s;
        &:hover{
          width: 190px;
          border-top-right-radius: 30px;
        }
      }
    }
    .right-title{
      width: 50%;
      img{
        width: 100%;
      }
    }
  }
}