.third-section {
  text-align: center;
  font-family: "Roboto-Medium";
  margin-top: 250px;

  h2 {
    font-size: 40px;
    font-weight: 400;
  }

  p {
    font-size: 16px;
    margin-top: 0;
  }

  .characteristics {
    display: flex;
    width: 65%;
    justify-content: space-between;
    margin: auto;
    margin-top: 100px;

    h2 {
      margin: 0 0 10px;
      font-size: 54px;
    }

    span {
      font-size: 17px;
    }
  }

  button {
    margin-top: 100px;
    margin-bottom: 100px;
    border: 0;
    width: 23%;
    height: 50px;
    background-color: black;
    color: white;
    font-size: 12px;
    transition: 0.3s;

    &:hover {
      width: 26%;
      border-top-right-radius: 30px;
      border-bottom-left-radius: 30px;
    }
  }
  section {
    display: flex;
    text-align: start;
    margin-top: 130px;
    align-items: start;
    margin-left: 100px;
    justify-content: start;
    .left-section{
      display: flex;
      flex-direction: column;
      width: 50%;
      span{
        font-size: 15px;
      }
      h2{
        font-size: 54px;
        margin: 15px 0 30px 0;
      }
      div{
        width: 48%;
        margin-left: 100px;
        margin-top: 30px;
        h3{
          font-size: 26px;
          font-weight: 400;
        }
        p{
          line-height: 27px;
          font-size: 17px;
        }
      }
    }
    .right-section{
      img{
        width: 115%;
      }
    }
  }
}