footer{
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
  padding: 80px 100px 88px 100px;
  font-family: Roboto-Medium;
  margin-top: 5px;
  .left-footer{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 45%;
    h2{
      font-size: 54px;
    }
  }
  .right-footer{
    display: flex;
    flex-direction: column;
    width: 55%;
    .right-top-footer{
      display: flex;
      justify-content: space-between;
      width: 70%;
      div{
        span{
          color:rgba(128, 128, 128, 1);
        }
        p{
          margin: 15px 0 15px 0;
          font-size: 15px;
          &:first-of-type{
            margin-top: 23px;
          }
        }
      }
    }
    .right-bottom-footer{
      margin-top: 200px;
      div{
        display: flex;
        justify-content: space-between;
        font-size: 15px;
        margin-bottom: 40px;
        p{
          line-height: 26px;
        }
      }
      span{
        font-size: 12px;
      }
    }
  }
}