.foto-blocks{
  text-align: center;
  font-family: Roboto-Medium;
  margin-top: 160px;
  h2{
    font-size: 29px;
    font-weight: 400;
    margin: 0 0 40px 0;
  }
  section{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .block-foto{
      display: flex;
      flex-direction: column;
      width: 24.7%;
      img{
        width: 100%;
        &:last-of-type{
          margin-top: 5px;
        }
      }
    }
  }
}